:host {
  display: flex;
  flex-direction: row;
  flex: 1;
  min-height: 0; // Allows proper scrolling in flex container
}

.docs-content {
  flex: 1;
  overflow-y: auto;
  overflow-x: hidden;
  background: var(--surface-page);
}

.docs-container {
  max-width: 900px;
  margin: 0 auto;
  padding: 2rem;

  @media (max-width: 640px) {
    padding: 1rem;
  }
}

// Mobile sidebar toggle
@media (max-width: 1024px) {
  :host {
    position: relative;
  }

  .docs-content {
    width: 100%;
  }
}
