:host {
  display: flex;
  flex-direction: row;
  flex: 1;
  min-height: 0;
}

.docs-content {
  flex: 1;
  overflow-y: auto;
  overflow-x: hidden;
  background: var(--surface-page);
}

.docs-container {
  max-width: 900px;
  margin: 0 auto;
  padding: 2rem;
}
@media (max-width: 640px) {
  .docs-container {
    padding: 1rem;
  }
}

@media (max-width: 1024px) {
  :host {
    position: relative;
  }
  .docs-content {
    width: 100%;
  }
}
